@font-face{font-family:"fang";src:url(../../bundle/fonts/fangzheng.TTF)format("truetype")}
.container{background: url('../img/bg.png') center center repeat;overflow: hidden;}
.center-box{width:450px;height:450px;left:50%;top:50%;margin-left:-225px;margin-top:-225px;font-family:"fang";}
.center-box .head{margin:0 auto;width:300px;font-size:38px;height:40px;line-height:40px}
.center-box .small-head{margin:22px auto;}
.userform{overflow:hidden;zoom:1;}
.userform p{height:50px;line-height:50px;text-align:left;overflow:hidden;zoom:1;margin-bottom:5px;}
.userform input,.yanzheng input{width:55%;height:40px;border-radius:5px;box-shadow:0;border:none;padding-left:5px;}
.yanzheng input{width:24%;}
#yzm{width:24%;height:40px;}
.userform span{width:24%;height:40px;vertical-align:middle;display:inline-block;line-height:40px;text-align:center}
.userform .submit input{padding-left:0;width:55%;background:#ff6600;font-weight:bold;margin:0 auto;margin-top:5px;cursor: pointer}
.footer{top:78.4%;left:0;color:#616e82}
.newsname,.newspwd,.newsyzm{width:250px;height:38px;background:white;position:absolute;top:109px;left:385px;text-align:center;line-height:40px;border-radius:5px;border:1px solid #ffa89a;color:#ffa89a;font-size:14px;display:none}
.newspwd{top:164px;}
.newsyzm{top:217px}
